<template>
  <div class="page-content" :class="[isMobile ? 'col-12 p-0' : 'col-10 offset-1 py-2 px-0']">
    <ActionForm :user="userInfo"/>
    <PostView :userInfo="userInfo"/>
  </div>
</template>

<script>
import ActionForm from "./ActionForm";
import PostView from "./PostView";
import { isMobile } from "../utilities";

export default {
  name: "PageContent",
  components: {
    ActionForm,
    PostView
  },
  data() {
    return {
      isMobile: isMobile.any()
    }
  },
  props: {
    userInfo: Object
  }
};
</script>

<style>
</style>



